<template>
  <div class="container">
    <!-- 标题栏 -->
    <van-nav-bar fixed title="收货地址" left-arrow @click-left="$router.back()">
      <template #right v-if="addressList.length">
        <div @click="manageAdd" :style="{ color: manageMode ? 'red' : '' }">
          {{ !manageMode ? "管理" : "完成" }}
        </div>
      </template>
    </van-nav-bar>

    <!-- 地址列表 -->
    <div class="add-list" v-if="addressList.length">
      <!-- add-itm -->
      <div
        class="add-item"
        v-for="(item, index) in addressList"
        :key="index"
      >
        <!-- 上部分 地址信息 编辑按钮 -->
        <div class="add-card">
          <!-- 左面 收货人信息 -->
          <div class="add-info" @click="chooseThisAdd(item.address_id)">
            <!-- 姓名 手机号 默认 -->
            <div class="contact">
              <span class="name truncate">{{ item.name }}</span>
              <span class="phone">{{ item.phone }}</span>
              <span class="iconfont icon-moren" v-if="item.address_id === defaultAddId"></span>
            </div>

            <!-- 地址信息 -->
            <div class="add-detail wrap-content">
              <div class="region">
                <span>{{ item.region.province }}</span>
                <span>{{ item.region.city }}</span>
                <span>{{ item.region.region }}</span>
              </div>
              <div class="detail">{{ item.detail }}</div>
            </div>
          </div>

          <!-- 右面 编辑按钮 -->
          <div class="edit" @click="editAddress(item.address_id)">
            <van-icon name="edit" size="6vw" />
          </div>
        </div>

        <!-- 分割线 -->
        <div class="line"></div>

        <!-- 下部分 默认 删除功能 -->
        <div class="item-actions" v-if="manageMode">
          <!-- 设为默认 -->
          <div class="default">
            <label
              class="item-radio"
              @click="setDefaultAdd(item.address_id)"
            >
              <input
                type="radio"
                class="radio"
                name="setAsDefault"
                :checked="item.address_id === defaultAddId"
              />
              <span class="text">设为默认</span>
            </label>
          </div>
          <!-- 删除 -->
          <div class="del-add" @click="delAddress(item.address_id)">
            <van-icon name="delete-o" />
            <span class="title">删除</span>
          </div>
        </div>
      </div>
    </div>

    <!-- 无地址显示 -->
    <div v-else>亲，暂无收货地址</div>

    <!-- 添加地址按钮 -->
    <div class="btn-area">
      <div class="btn-create" @click="$router.push('/createadd')">
        <span>添加新地址</span>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: 'AddressIndex',

  data () {
    return {
      manageMode: false
    }
  },

  computed: {
    ...mapState('address', [
      'addressList',
      'defaultAddId',
      'chosenAddId'
    ])
  },

  methods: {
    // 管理模式
    manageAdd () {
      this.manageMode = !this.manageMode
    },

    // 编辑地址
    editAddress (addressId) {
      this.$router.push(`/editadd/?editedid=${addressId}`)
    },

    // 删除地址
    delAddress (addressId) {
      this.$dialog.confirm({
        message: '确认要删除吗'
      })
        .then(() => {
          // on confirm
          this.$store.dispatch('address/delAddress', addressId)
        })
        .catch(() => {
          // on cancel
        })
    },

    chooseThisAdd (addressId) {
      // 如果是从订单结算面跳转过来，则点击地址后返回
      if (this.$route.query.backUrl) {
        this.$store.commit('address/setChosenAdd', addressId)
        this.$router.back()
      }
    },

    setDefaultAdd (addressId) {
      this.$store.dispatch('address/setDefaultAdd', addressId)
    }
  },

  created () {
    this.$store.dispatch('address/getAddress')
    this.$store.dispatch('address/getDefaultAdd')
  }
}
</script>

<style lang="less" scoped>
.container {
  min-height: 100vh;
  padding: 48px 21px 60px;
  background: #fafafa;
}
.truncate {
  display: inline-block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  max-width: 12ch;
}
.wrap-content {
  display: inline-block;
  max-width: 100%;
  word-wrap: break-word;
}

.add-item {
  padding: 6px 6px 0;
  margin-top: 10.8px;
  border-radius: 8.1px;
  background-color: #fff;
  box-shadow: 0 1px 5px 0px rgba(0, 0, 0, 0.2);
}

.add-card {
  display: flex;
  justify-content: space-between;
  .add-info {
    max-width: 80%;
  }
  .edit {
    display: flex;
    align-items: center;
  }
}

.contact {
  display: flex;
  height: 16.2px;
  font-size: 16.2px;
  line-height: 16.2px;
  .name {
    font-weight: 550;
  }
  .phone {
    margin: 0 6px;
  }
  .icon-moren {
    color: #f9211c;
  }
}

.add-detail {
  font-size: 14.1px;
  color: #555;
  .region {
    margin: 5.1px 0;
    span {
      margin-right: 5.1px;
    }
  }
}

.line {
  margin: 6px 0;
  border-bottom: 1px solid #cccccc;
}

.item-actions {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 6px;
  font-size: 14.1px;
}

.btn-area {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 60px;
  background-color: #fff;
  .btn-create {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 90%;
    font-size: 15px;
    height: 39px;
    line-height: 39px;
    border-radius: 24px;
    background: linear-gradient(to right, #f9211c, #ff6335);
    color: #fff;
  }
}
</style>
